import React, { useEffect, useState, useRef } from "react";
import "leaflet/dist/leaflet.css";
import "leaflet-routing-machine/dist/leaflet-routing-machine.css";
import L from "leaflet";
import locations from "/src/storage/map.json";

// Fix para iconos en producción
import icon from "leaflet/dist/images/marker-icon.png";
import iconShadow from "leaflet/dist/images/marker-shadow.png";
import iconRetina from "leaflet/dist/images/marker-icon-2x.png";

// Configuración global de iconos
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl: iconRetina,
  iconUrl: icon,
  shadowUrl: iconShadow,
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  popupAnchor: [1, -34],
  shadowSize: [41, 41],
});

const Map = () => {
  const mapRef = useRef(null);
  const [map, setMap] = useState(null);
  const [transportMode, setTransportMode] = useState("foot");
  const [userLocation, setUserLocation] = useState(null);
  const [favorites, setFavorites] = useState([]);
  const routingControlRef = useRef(null);
  const [isLoading, setIsLoading] = useState(true);

  // Cargar favoritos del usuario
  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("user"));
    if (user) {
      const userFavorites =
        JSON.parse(localStorage.getItem(`favorites_${user.email}`)) || [];
      setFavorites(userFavorites);
    }
  }, []);

  // Inicialización del mapa
  useEffect(() => {
    if (!mapRef.current || map) return;

    const mapInstance = L.map(mapRef.current, {
      center: [40.9634, -5.6634],
      zoom: 13,
      preferCanvas: true, // Mejor rendimiento para muchos marcadores
    });

    // Capa base de OpenStreetMap
    L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
      attribution:
        '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a>',
      maxZoom: 19,
    }).addTo(mapInstance);

    setMap(mapInstance);
    setIsLoading(false);

    // Obtener ubicación del usuario
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          setUserLocation([latitude, longitude]);
          L.marker([latitude, longitude], {
            title: "Tu ubicación",
          })
            .addTo(mapInstance)
            .bindPopup("Tu ubicación actual");
        },
        (error) => {
          console.warn("No se pudo obtener la ubicación:", error);
        }
      );
    }

    return () => {
      if (mapInstance) {
        mapInstance.remove();
      }
    };
  }, []);

  // Añadir marcadores y funcionalidad
  useEffect(() => {
    if (!map || isLoading) return;

    // Limpiar marcadores existentes
    map.eachLayer((layer) => {
      if (layer instanceof L.Marker) {
        map.removeLayer(layer);
      }
    });

    // Añadir nuevos marcadores
    locations.forEach((location) => {
      const marker = L.marker(
        [location.coordenadas.lat, location.coordenadas.lng],
        {
          title: location.nombre,
          icon: L.divIcon({
            className: `custom-marker ${location.tipo} ${
              favorites.includes(location.id) ? "favorite" : ""
            }`,
            html: `<div>${location.nombre}</div>`,
            iconSize: [32, 32],
          }),
        }
      ).addTo(map);

      // Contenido del popup
      const popupContent = `
        <div class="map-popup">
          <h3>${location.nombre}</h3>
          <p>${location.direccion}</p>
          <p>Teléfono: ${location.telefono}</p>
          <div class="popup-actions">
            <button class="favorite-btn ${
              favorites.includes(location.id) ? "active" : ""
            }" 
              data-id="${location.id}">
              ★ Favorito
            </button>
            <button class="route-btn" 
              data-lat="${location.coordenadas.lat}" 
              data-lng="${location.coordenadas.lng}">
              Calcular Ruta
            </button>
          </div>
        </div>
      `;

      marker.bindPopup(popupContent);
    });

    // Manejadores de eventos delegados
    const handlePopupClick = (e) => {
      const target = e.target;

      if (target.classList.contains("favorite-btn")) {
        const locationId = parseInt(target.dataset.id);
        toggleFavorite(locationId);
      }

      if (target.classList.contains("route-btn")) {
        const lat = parseFloat(target.dataset.lat);
        const lng = parseFloat(target.dataset.lng);
        calculateRoute(lat, lng);
      }
    };

    map.getContainer().addEventListener("click", handlePopupClick);

    return () => {
      map.getContainer().removeEventListener("click", handlePopupClick);
    };
  }, [map, favorites, transportMode]);

  const toggleFavorite = (id) => {
    const user = JSON.parse(localStorage.getItem("user"));
    if (!user) {
      alert("Debes iniciar sesión para usar favoritos");
      return;
    }

    const newFavorites = favorites.includes(id)
      ? favorites.filter((fId) => fId !== id)
      : [...favorites, id];

    localStorage.setItem(
      `favorites_${user.email}`,
      JSON.stringify(newFavorites)
    );
    setFavorites(newFavorites);
  };

  const calculateRoute = async (lat, lng) => {
    if (!userLocation || !map) return;

    try {
      const LRouting = await import("leaflet-routing-machine");

      // Eliminar ruta existente
      if (routingControlRef.current) {
        map.removeControl(routingControlRef.current);
      }

      // Crear nueva ruta
      routingControlRef.current = LRouting.control
        .routing({
          waypoints: [
            L.latLng(userLocation[0], userLocation[1]),
            L.latLng(lat, lng),
          ],
          routeWhileDragging: true,
          showAlternatives: false,
          fitSelectedRoutes: true,
          collapsible: true,
          addWaypoints: false,
          draggableWaypoints: false,
          router: new LRouting.OSRMv1({
            profile:
              transportMode === "car"
                ? "driving"
                : transportMode === "bike"
                ? "cycling"
                : "foot",
          }),
        })
        .addTo(map);
    } catch (error) {
      console.error("Error al cargar routing machine:", error);
    }
  };

  if (isLoading) {
    return (
      <div className="map-loading">
        <div className="loading-message">Cargando mapa...</div>
      </div>
    );
  }

  return (
    <div className="map-container">
      <div ref={mapRef} className="map-view"></div>

      <div className="map-controls">
        <h3>Modo de transporte:</h3>
        <select
          value={transportMode}
          onChange={(e) => setTransportMode(e.target.value)}
          className="transport-select"
        >
          <option value="foot">A pie</option>
          <option value="bike">Bicicleta</option>
          <option value="car">Coche</option>
        </select>
      </div>

      <style jsx>{`
        .map-container {
          position: relative;
          width: 100%;
          height: 100vh;
        }

        .map-view {
          width: 100%;
          height: 100%;
          min-height: 500px;
        }

        .map-loading {
          display: flex;
          justify-content: center;
          align-items: center;
          height: 100vh;
        }

        .map-controls {
          position: absolute;
          top: 20px;
          right: 20px;
          background: white;
          padding: 10px;
          border-radius: 5px;
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
          z-index: 1000;
        }

        .transport-select {
          padding: 5px;
          border-radius: 3px;
          border: 1px solid #ccc;
        }

        .custom-marker {
          display: flex;
          justify-content: center;
          align-items: center;
          background: #4caf50;
          border-radius: 50%;
          color: white;
          font-weight: bold;
          font-size: 12px;
        }

        .custom-marker.hospital {
          background: #ff5252;
        }

        .custom-marker.farmacia {
          background: #2196f3;
        }

        .custom-marker.favorite {
          border: 2px solid #ffeb3b;
        }

        .map-popup {
          min-width: 200px;
        }

        .popup-actions {
          margin-top: 10px;
          display: flex;
          gap: 5px;
        }

        .favorite-btn,
        .route-btn {
          padding: 5px 10px;
          border: none;
          border-radius: 3px;
          cursor: pointer;
        }

        .favorite-btn {
          background: #f5f5f5;
        }

        .favorite-btn.active {
          background: #ffeb3b;
        }

        .route-btn {
          background: #4caf50;
          color: white;
        }
      `}</style>
    </div>
  );
};

export default Map;
